import React from 'react';
import moment from 'moment';

class LogRow extends React.Component {
  render() {
    return (
      <li className="list-group-item">
          <div className="row">
              <div className="col-xs-4 col-sm-2">진통 시간</div>
              <div className="col-xs-8 col-sm-10"><time>{moment(this.props.log.start).format('HH:mm:ss')}</time> ~ <time>{moment(this.props.log.end).format('HH:mm:ss')}</time></div>
          </div>
          <div className="row">
              <div className="col-xs-4 col-sm-2">진통 간격</div>
              <div className="col-xs-8 col-sm-10">{this.props.log.gap ? moment(this.props.log.gap).format('HH:mm:ss') : ''}</div>
          </div>
      </li>
    );
  }
}


class LogSection extends React.Component {
  constructor(props) {
    super(props);
    this.handleReset = this.handleReset.bind(this);
  }

  handleReset() {
    this.props.onReset();
  }

  render() {
    const ROWS = [];

    this.props.logs.forEach((log) => {
      ROWS.push(<LogRow key={log.start} log={log} />);
    });

    return (
      <div id="log" className='hidden'>
        <ul className="list-group">{ROWS}</ul>
        <div className="text-center">
            <button type="button" className="btn btn-primary" onClick={this.handleReset}>초기화</button>
        </div>
      </div>
    );
  }
}

export default LogSection;


