import React from 'react';
import {Line} from 'react-chartjs-2';

function secToHHMMSS(sec_num) {
  let hours = Math.floor(sec_num / 3600),
      minutes = Math.floor((sec_num - (hours * 3600)) / 60),
      seconds = sec_num - (hours * 3600) - (minutes * 60);

  if (hours < 10) {
    hours = '0' + hours;
  }

  if (minutes < 10) {
    minutes = '0' + minutes;
  }

  if (seconds < 10) {
    seconds = '0' + seconds;
  }

  const time = hours + ':' + minutes + ':' + seconds;

  return time;
}


class ChartSection extends React.Component {
  render() {
    const lineDatas = {
      datasets: [
        {
          fill: false,
          backgroundColor: 'rgb(255, 99, 132)',
          borderColor: 'rgb(255, 99, 132)',
          data: this.props.chartDatas
        }
      ]
    };

    const options = {
      scales: {
        xAxes: [{
          type: 'linear',
          ticks: {
            stepSize: 1
          }
        }],
        yAxes: [{
            type: 'linear',
            ticks: {
              callback: function(value, index, values) {
                  return secToHHMMSS(value);
              }
            }
        }]
      },
      elements: {
          line: {
              tension: 0,
              showLines: false
          }
      },
      animation: {
          duration: 0
      },
      hover: {
          animationDuration: 0
      },
      responsive: false,
      responsiveAnimationDuration: 0,
      legend: {
          display: false
      }
    };

    return (
      <div id="chart" className='hidden'>
        <figure>
          <div id="gap-chart">
            {this.props.chartDatas.length > 0 &&
              <Line data={lineDatas} options={options} redraw={true} width={window.innerWidth - 30} height={window.innerHeight - 61 - 100  - 20 - 40} />
            }
          </div>
          <figcaption className="text-center">x: 진통 주기, y: 진통 간격</figcaption>
        </figure>
      </div>
    );
  }
}

export default ChartSection;


