import React from 'react';

class Nav extends React.Component {
  constructor(props) {
    super(props);
    this.handleNav = this.handleNav.bind(this);
  }

  handleNav(e) {
    e.preventDefault();

    const contractionTimerWrapper = document.querySelector('.contractionTimerWrapper'),
          timerEl = document.getElementById('timer'),
          logEl = document.getElementById('log'),
          chartEl = document.getElementById('chart');

    let liEl = e.target.parentElement,
        ulEl = liEl.parentElement,
        liEls = ulEl.querySelectorAll('li');

    switch (e.target.getAttribute('href')) {
      case '#timer':
        contractionTimerWrapper.classList.add('timer');
        timerEl.classList.remove('hidden');
        timerEl.classList.add('show');
        logEl.classList.add('hidden');
        chartEl.classList.add('hidden');
        break;
      case '#log':
        contractionTimerWrapper.classList.remove('timer');
        logEl.classList.remove('hidden');
        logEl.classList.add('show');
        timerEl.classList.add('hidden');
        chartEl.classList.add('hidden');
        break;
      case '#chart':
        contractionTimerWrapper.classList.remove('timer');
        chartEl.classList.remove('hidden');
        chartEl.classList.add('show');
        timerEl.classList.add('hidden');
        logEl.classList.add('hidden');
        break;
      default:
    }

    liEls.forEach((li) => {
      li.classList.remove('active');
    });

    liEl.classList.add('active');
  }

  render() {
    return (
      <nav className="container-fluid">
        <ul className="nav nav-pills" onClick={this.handleNav}>
          <li className="active">
            <a href="#timer" className="text-center">타이머</a>
          </li>
          <li>
            <a href="#log" className="text-center">기록</a>
          </li>
          <li>
            <a href="#chart" className="text-center">차트</a>
          </li>
        </ul>
      </nav>
    );
  }
}

export default Nav;


