import React from 'react';
import moment from 'moment';
import Nav from './Nav';
import ChartSection from './ChartSection';
import LogSection from './LogSection';
import TimerSection from './TimerSection';


let logs = [];
let log = {};
let chartDatas = [];
let chartData = {};
let count = 0;


class ContractionTimer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      timer: '00:00:00',
      lastGap: '00:00:00',
      isStart: false,
      startAt: 0,
      lapTime: 0,
      logs: [],
      chartDatas: []
    };

    this.handleStartStop = this.handleStartStop.bind(this);
    this.handleReset = this.handleReset.bind(this);
  }

  tick() {
    this.setState({timer: moment( this.state.lapTime + (this.state.startAt ? moment().valueOf() - this.state.startAt : 0) ).subtract(9, 'hours').format('HH:mm:ss')});
  }

  timerReset() {
    this.setState({
      lapTime: 0,
      startAt: 0
    });
  }

  timerStart() {
    this.timerReset();
    this.timerID = setInterval(() => this.tick(), 1);

    this.setState({
      startAt: moment().valueOf()
    });
  }

  timerStop() {
    this.setState({
      lapTime: this.state.startAt ? this.state.lapTime + moment().valueOf() - this.state.startAt : this.state.lapTime,
      startAt: 0
    });

    clearInterval(this.timerID);
  }

  handleStartStop() {
    this.setState(prevState => ({
      isStart: !prevState.isStart
    }));

    if (this.state.isStart) {
      this.timerStop();
      log.end = moment().valueOf();
      logs.push(log);
      log = {};

      this.setState({
        logs: logs
      });

      if (count) {
        chartDatas.push(chartData);
        chartData = {};

        this.setState({
          chartDatas: chartDatas
        });
      }
    } else {
      this.timerStart();
      log.start = moment().valueOf();

      if (logs.length) {
        log.gap = moment(moment(log.start).diff(moment(logs[logs.length - 1]['start']))).subtract(9, 'hours');

        this.setState({
          lastGap: moment(log.gap).format('HH:mm:ss')
        });

        count += 1;
        chartData.x = count;
        chartData.y = parseInt((log.start - logs[logs.length - 1]['start']) / 1000, 10);
      }
    }
  }

  handleReset() {
    logs = [];
    log = {};
    chartDatas = [];
    chartData = {};
    count = 0;
    this.timerStop();
    this.timerReset();

    this.setState({
      timer: '00:00:00',
      lastGap: '00:00:00',
      isStart: false,
      logs: [],
      chartDatas: []
    });
  }

  render() {
    return (
      <div className="contractionTimerWrapper timer">
        <main className="container-fluid">
          <TimerSection timer={this.state.timer} lastGap={this.state.lastGap} isStart={this.state.isStart} onStartStopToggle={this.handleStartStop} />
          <LogSection logs={this.state.logs} onReset={this.handleReset} />
          <ChartSection chartDatas={this.state.chartDatas} />
        </main>
        <footer className="navbar navbar-default navbar-fixed-bottom">
          <Nav />
        </footer>
      </div>
    );
  }
}

export default ContractionTimer;



