import React from 'react';

class TimerSection extends React.Component {
  constructor(props) {
    super(props);
    this.handleStartStop = this.handleStartStop.bind(this);
  }

  handleStartStop() {
    this.props.onStartStopToggle();
  }

  render() {
    return (
      <div id="timer">
        <div id="clock" className="text-center">{this.props.timer}</div>

        <div className="panel panel-info text-center">
          <div className="panel-heading">
            <h1 className="panel-title">최근 진통 간격</h1>
          </div>
          <div className="panel-body">{this.props.lastGap}</div>
        </div>

        <div className="row">
          <div className="col-xs-6 col-sm-6 col-xs-offset-3 col-sm-offset-3">
            <button type="button" className="btn btn-primary btn-block btn-lg" onClick={this.handleStartStop}>
              {this.props.isStart ? '정지' : '시작'}
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default TimerSection;


